import { create } from 'zustand';

interface GoalStore {
    isModalOpen: boolean;
    openModal: () => void;
    closeModal: () => void;
}

export const useGoalStore = create<GoalStore>()((set) => ({
    isModalOpen: false,
    openModal: () => set({ isModalOpen: true }),
    closeModal: () => set({ isModalOpen: false }),
}));
